'use client';
import React, { useState, ReactNode } from 'react';
import { 
  MessageSquare, 
  Settings, 
  User, 
  Info,
  Menu,
  X
} from 'lucide-react';
import { Button } from '@/app/components/ui/button';
import { ScrollArea } from '@/app/components/ui/scroll-area';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

interface AdminLayoutProps {
  children: ReactNode;
}

const AdminLayout = ({ children }: AdminLayoutProps) => {
  const [isSidebarOpen, setIsSidebarOpen] = useState(true);
  const pathname = usePathname();

  const menuItems = [
    { icon: MessageSquare, label: 'Chat', href: '/admin/chat' },
    { icon: Settings, label: 'Settings', href: '/admin/settings' },
    { icon: User, label: 'Profile', href: '/admin/profile' },
    { icon: Info, label: 'About', href: '/admin/about' }
  ];

  return (
    <div className="min-h-screen flex bg-gray-50">
      {/* Sidebar */}
      <aside 
        className={`bg-purple-900 w-64 transition-all duration-300 ${
          isSidebarOpen ? 'translate-x-0' : '-translate-x-full'
        } fixed md:relative md:translate-x-0 min-h-screen z-20`}
      >
        <div className="p-4 h-full flex flex-col">
          {/* Logo/Header */}
          <div className="flex items-center justify-between mb-6">
            <h1 className="text-xl font-bold text-white">Admin Dashboard</h1>
            <Button 
              variant="ghost" 
              size="icon"
              className="md:hidden text-white hover:bg-purple-800"
              onClick={() => setIsSidebarOpen(false)}
            >
              <X className="h-5 w-5" />
            </Button>
          </div>

          {/* Navigation Menu */}
          <ScrollArea className="flex-1 -mx-4">
            <div className="px-4 space-y-2">
              {menuItems.map((item) => {
                const isActive = pathname === item.href;
                return (
                  <Link href={item.href} key={item.label}>
                    <Button 
                      variant="ghost" 
                      className={`w-full justify-start ${
                        isActive 
                          ? 'bg-purple-800 text-white' 
                          : 'text-purple-100 hover:bg-purple-800 hover:text-white'
                      }`}
                      onClick={() => setIsSidebarOpen(false)}
                    >
                      <item.icon className="mr-2 h-4 w-4" />
                      {item.label}
                    </Button>
                  </Link>
                );
              })}
            </div>
          </ScrollArea>

          {/* User Info */}
          <div className="border-t border-purple-800 pt-4 mt-4">
            <div className="flex items-center space-x-3 text-purple-100 px-2">
              <div className="w-8 h-8 rounded-full bg-purple-800 flex items-center justify-center">
                <User className="h-4 w-4" />
              </div>
              <div>
                <p className="text-sm font-medium">Admin User</p>
                <p className="text-xs opacity-75">admin@example.com</p>
              </div>
            </div>
          </div>
        </div>
      </aside>

      {/* Main Content Area */}
      <main className="flex-1 flex flex-col relative">
        {/* Mobile Header */}
        <div className="md:hidden bg-white border-b p-4">
          <Button 
            variant="ghost" 
            size="icon"
            onClick={() => setIsSidebarOpen(true)}
          >
            <Menu className="h-5 w-5" />
          </Button>
        </div>

        {/* Page Content */}
        <div className="flex-1 p-6 pb-0">
          {children}
        </div>
      </main>
    </div>
  );
};

export default AdminLayout;